section {
    &.userdetail-section {
        padding-top: 5rem;

        .userprofile-block {
            margin-top: 45px;

            .userprofile-select {
                max-width: 100px;
                margin: auto;
                position: relative;

                img {
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    overflow: hidden;
                    object-fit: cover;
                    object-position: center;
                    border: 2px solid #fff;
                    box-shadow: 0px 5px 12px 0px #00000021;
                }

                .userprofile-control {
                    width: 30px;
                    height: 30px;
                    overflow: hidden;
                    border-radius: 50%;
                    position: relative;
                    cursor: pointer;
                    outline: none;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    border: 2px solid #fff;
                    background: #cb007b;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    &:before {
                        width: 100%;
                        height: 100%;
                        background: #cb007b;
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        border-radius: 50%;
                        z-index: 11;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }

                    &::-webkit-file-upload-button {
                        background: #cb007b;
                        font-size: 0;
                        width: 100%;
                        height: 100%;
                        border: none;
                        outline: none;
                        cursor: pointer;
                        background-image: url('../../assets/images/upload.svg');
                        background-size: 14px;
                        background-repeat: no-repeat;
                        background-position: center;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }

            h5.username-txt {
                margin-bottom: 30px;
                text-align: center;
                margin-top: 1.25rem;
                font-size: 2rem;
            }
        }

        .border-seprater {
            border: 1px solid #dedede;
        }

        .userdetails-tabs {
            display: flex;
            align-items: center;
            justify-content: space-around;
        }
    }
}